<template>
  <div class="boardContent">
    <!-- // 查看 弹出框 -->
    <div class="infocontentWrapper">
      <div class="infotitle">删掉公告标题</div>
      <div class="infocontent">{{ ticketContent.title }}</div>
    </div>
    <div class="infocontentWrapper">
      <div class="infotitle">公告内容</div>
      <!-- <div class="infocontent">{{ ticketContent.content.text }}</div> -->
      <div class="infocontent">{{ ticketContent.content }}</div>
    </div>
    <div class="infocontentWrapper">
      <div class="infotitle">创建原因</div>
      <div class="infocontent">{{ ticketContent.create_reason }}</div>
    </div>
    <div class="infocontentWrapper">
      <div class="infotitle">发布平台</div>
      <div class="infocontent">
        {{ infoPlatform(ticketContent.platform) }}
      </div>
    </div>
    <div class="infocontentWrapper">
      <div class="infotitle">发布状态</div>
      <div class="infocontentUrgent" v-show="isUrgent">
        {{ infoStatus(ticketContent.status) }}
      </div>
      <div class="infocontentActivate" v-show="isActivate">
        {{ infoStatus(ticketContent.status) }}
      </div>
      <div class="infocontentInActivate" v-show="isInActivate">
        {{ infoStatus(ticketContent.status) }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-ProductTicketContent",
  props: {
    ticketContent: {
      type: [Object, Array],
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      isUrgent: false,
      isActivate: false,
      isInActivate: false,
    };
  },
  methods: {
    infoPlatform(code) {
      if (code == 10000) {
        return "所有平台";
      }
      if (code == 10001) {
        return "IOS";
      }
      if (code == 10002) {
        return "小程序";
      }
    },
    infoStatus(code) {
      if (code == 11000) {
        this.isUrgent = true;
        this.isActivate = false;
        this.isInActivate = false;
        return "紧 急";
      }
      if (code == 11001) {
        this.isUrgent = false;
        this.isActivate = true;
        this.isInActivate = false;
        return "使用中";
      }
      if (code == 11002) {
        this.isUrgent = false;
        this.isActivate = false;
        this.isInActivate = true;
        return "已停用";
      }
    },
  },
};
</script>

